import {
  Avatar,
  Button,
  Container,
  IconButton,
  Paper,
  TextField,
  Typography
} from '@material-ui/core'
import AddIcon from '@material-ui/icons/Add'
import CloseIcon from '@material-ui/icons/Close'
import { Formik } from 'formik'
import { helperTextStyles } from 'pages/SignIn/constants'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import AppHeader from '../../components/Header/AppHeader'
import ImageUpload from './components/imageUpload'
import { IMAGE_TYPE, recipeStyles, validationRecipeSchema } from './constant'
import { CreateRecipe } from './redux/actions'

export default props => {
  const classes = recipeStyles()
  const helperTextStyle = helperTextStyles()
  const dispatch = useDispatch()
  const user = useSelector(state => state.Auth.user)
  const history = useHistory()

  const addPictureStep = (steps, index, picture, setFieldValue) => {
    steps[index].image = picture
    setFieldValue('steps', steps)
  }

  const removePictureStep = (steps, index, setFieldValue) => {
    steps[index].image = null
    setFieldValue('steps', steps)
  }

  const submitRecipe = values => {
    dispatch(
      CreateRecipe.get({
        ...values,
        ingredients: values.ingredients.join('|'),
        categories: values.categories.join('|'),
        userId: user?.id
      })
    )
  }

  if (!user) {
    return (
      <>
        <AppHeader />
        <Container
          maxWidth="md"
          className={classes.root}
          style={{ textAlign: 'center' }}
        >
          <Typography variant="body1" style={{ margin: 28 }}>
            Bn cha ng nhp. Vui lng ng nhp  to bi vit.
          </Typography>
          <Button
            variant="contained"
            color="primary"
            className={classes.button}
            onClick={() => history.push('/signin')}
          >
            ng nhp
          </Button>
        </Container>
      </>
    )
  }

  return (
    <>
      <AppHeader />
      <Formik
        initialValues={{
          title: '',
          description: '',
          avatar: null,
          ration: '',
          cookingTime: 20,
          difficultLevel: 1,
          ingredients: [''],
          categories: [],
          hashtags: '',
          steps: [{ stt: 1, making: '', image: null }]
        }}
        isInitialValid={false}
        validationSchema={validationRecipeSchema}
        onSubmit={values => submitRecipe(values)}
      >
        {({
          handleChange,
          handleBlur,
          handleSubmit,
          values,
          isValid,
          errors,
          touched,
          setFieldTouched,
          setFieldValue
        }) => {
          return (
            <Container maxWidth="md" className={classes.root}>
              <Typography variant="h5">To bi ng</Typography>
              <ImageUpload
                type={IMAGE_TYPE.WIDE}
                onChange={handleChange('avatar')}
                onRemove={() => setFieldValue('avatar', null)}
                value={values.avatar}
              />
              {errors.avatar && (
                <Typography variant="body2" className={classes.errorStyle}>
                  {errors.avatar}
                </Typography>
              )}

              <div className={classes.group}>
                <Typography variant="body1">
                  <strong>Tiu </strong>
                </Typography>
                <TextField
                  placeholder="Gi..."
                  variant="outlined"
                  fullWidth
                  className={classes.field}
                  helperText={errors.title && errors.title}
                  FormHelperTextProps={{ classes: helperTextStyle }}
                  value={values.title}
                  onChange={handleChange('title')}
                  onTouchStart={() => setFieldTouched('title')}
                  onBlur={handleBlur('title')}
                />
              </div>

              <div className={classes.group}>
                <Typography variant="body1">
                  <strong>M t</strong>
                </Typography>
                <TextField
                  placeholder="Mn n dnh cho ma h..."
                  variant="outlined"
                  multiline
                  rows={3}
                  fullWidth
                  className={classes.field}
                  value={values.description}
                  onChange={handleChange('description')}
                  onTouchStart={() => setFieldTouched('description')}
                  onBlur={handleBlur('description')}
                />
              </div>

              <div className={classes.group}>
                <Typography variant="body1" style={{ marginBottom: '0.75rem' }}>
                  <strong>Nguyn liu</strong>
                </Typography>
                {values.ingredients.length > 0 &&
                  values.ingredients.map((material, index) => (
                    <Paper
                      component="div"
                      elevation={0}
                      key={index}
                      className={classes.paper}
                    >
                      <TextField
                        placeholder="100g tht ba ch..."
                        variant="outlined"
                        fullWidth
                        className={classes.field}
                        helperText={
                          errors.ingredients &&
                          typeof errors.ingredients === 'object' &&
                          errors.ingredients[index] &&
                          errors.ingredients[index]
                        }
                        FormHelperTextProps={{ classes: helperTextStyle }}
                        value={material}
                        onTouchStart={() => setFieldTouched('ingredients')}
                        onChange={event => {
                          let ingres = values.ingredients
                          ingres[index] = event.target.value
                          setFieldValue('ingredients', ingres)
                        }}
                      />
                      <IconButton
                        color="primary"
                        className={classes.iconButton}
                        onClick={() => {
                          let ingres = values.ingredients
                          ingres.splice(index, 1)
                          setFieldValue('ingredients', ingres)
                        }}
                      >
                        <CloseIcon />
                      </IconButton>
                    </Paper>
                  ))}
                {errors.ingredients && typeof errors.ingredients === 'string' && (
                  <Typography variant="body1" className={classes.errorStyle}>
                    {errors.ingredients}
                  </Typography>
                )}
                <Button
                  size="small"
                  color="primary"
                  startIcon={<AddIcon />}
                  className={classes.add}
                  onClick={() => {
                    let ingres = values.ingredients
                    ingres.push('')
                    setFieldValue('ingredients', ingres)
                  }}
                >
                  Thm nguyn liu
                </Button>
              </div>

              <div className={classes.group}>
                <Typography variant="body1" style={{ marginBottom: '0.75rem' }}>
                  <strong>Cc bc thc hin</strong>
                </Typography>
                {values.steps.length > 0 &&
                  values.steps.map((step, i) => (
                    <div key={`step${step.stt}`}>
                      <Paper
                        component="div"
                        elevation={0}
                        className={classes.paper}
                      >
                        <Avatar className={classes.stepNum}>{i + 1}</Avatar>
                        <TextField
                          placeholder="S ch tht..."
                          variant="outlined"
                          fullWidth
                          className={classes.field}
                          helperText={
                            errors.steps &&
                            typeof errors.steps === 'object' &&
                            errors.steps[i]?.making &&
                            errors.steps[i].making
                          }
                          FormHelperTextProps={{ classes: helperTextStyle }}
                          value={step.making}
                          onTouchStart={() => setFieldTouched('steps')}
                          onChange={event => {
                            let steps = values.steps
                            steps[i].making = event.target.value
                            setFieldValue('steps', steps)
                          }}
                        />
                        <IconButton
                          color="primary"
                          className={classes.iconButton}
                          onClick={() => {
                            let steps = values.steps
                            steps.splice(i, 1)
                            setFieldValue('steps', steps)
                          }}
                        >
                          <CloseIcon />
                        </IconButton>
                      </Paper>
                      <ImageUpload
                        type={IMAGE_TYPE.NORMAL}
                        onChange={data =>
                          addPictureStep(values.steps, i, data, setFieldValue)
                        }
                        onRemove={() =>
                          removePictureStep(values.steps, i, setFieldValue)
                        }
                        style={{ marginLeft: 40 }}
                        value={step.image}
                      />
                    </div>
                  ))}
                {errors.steps && typeof errors.steps === 'string' && (
                  <Typography variant="body2" className={classes.errorStyle}>
                    {errors.steps}
                  </Typography>
                )}
                <Button
                  size="small"
                  color="primary"
                  startIcon={<AddIcon />}
                  className={classes.add}
                  onClick={() => {
                    let steps = values.steps
                    steps.push({
                      stt: values.steps.length + 1,
                      making: ''
                    })
                    setFieldValue('steps', steps)
                  }}
                >
                  Thm bc
                </Button>
              </div>

              <div className={classes.group} style={{ textAlign: 'center' }}>
                <Button
                  size="large"
                  variant="contained"
                  className={classes.button}
                  style={{
                    marginRight: 24,
                    backgroundColor: 'red',
                    color: 'white'
                  }}
                  onClick={() => history.goBack()}
                >
                  Hu
                </Button>
                <Button
                  size="large"
                  variant="contained"
                  color="primary"
                  className={classes.button}
                  disabled={!isValid}
                  onClick={handleSubmit}
                >
                  ng
                </Button>
              </div>
            </Container>
          )
        }}
      </Formik>
    </>
  )
}
